import * as d3 from "d3-selection";
import { scaleOrdinal, scaleLinear, schemeCategory10 } from "d3-scale";
import { json as d3json } from "d3-request";
import { StaticDrinkGraph } from "./DummyGraph";
let width = 500;
let height = 500;
let colorscale = scaleOrdinal(schemeCategory10);
let widthscale = scaleLinear().rangeRound([0, width]);
let chart = d3.select("body").append("svg")
    .attr("width", width)
    .attr("height", height);
d3json("data/dummy/dummy.json", function (response) {
    let graph = new StaticDrinkGraph(response);
    widthscale.domain([0, graph.nodes.length - 1]);
    let nodes = chart.selectAll(".node")
        .data(graph.nodes, function (d) { return "" + d.id; }); //calls the id getter
    nodes.enter().append("g")
        .classed("node", true)
        .append("circle")
        .attr("r", 5)
        .attr("cx", 0)
        .attr("cy", 0);
    console.log(nodes);
    let nodeUpdate = nodes.enter().merge(nodes);
    console.log(nodeUpdate);
    nodeUpdate
        .style("fill", function (d) {
        console.log(d);
        //we are letting TS check that d is the right type
        //and warn us if it is ever not
        console.log(d.type, d.id);
        console.log(colorscale(d.type));
        return colorscale(d.type);
    })
        .attr("y", 10)
        .attr("x", function (d, i) {
        return widthscale(i);
    });
});
